//componet lit
import { LitElement, html, css  } from 'lit-element';
import './r2-button-magic.js';
import './r2-input.js';
import './r2-avatars.js';

export class R2SearchCards extends LitElement {
    
    static styles = [css`
    :host{
        display: block;
        padding: 10px;
        padding-left: 230px;
    }

    /* Estilos para la versin responsive */
    @media (max-width: 768px) {
      :host {
        padding-left: 0px;
      }
    }

    .list{
        display: grid;
        grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
        gap: 16px;
      
    }

    form{
        display: flex;
        justify-content: center;
        align-items: center;
        margin-bottom: 16px;
        gap: 1.9rem;
    }

       
    `];
 
   static properties = {
        hover : { type: Boolean }
    };

    constructor(){
        super();
        this.hover = false;
    }

    render(){
        return html`
        <h1>${this.title}</h1>
        <form data-testid="form" class="serach">
            <r2-input placeholder="Cedula" id="cedula" value="00106740764"></r2-input>
            <r2-button-magic hover @click='${this.search}'> Buscar </r2-button-magic>
        </form>
        <ul  class="list">
           <slot></slot>
        </ul>
        `;
    }

    search(){
        const cedula = this.shadowRoot.getElementById('cedula').value;
        fetch(`/mora?cedula=${cedula}`).then(res => res.json()).then(data => {
            console.log(data);
            let items = data.data
            const list = this.shadowRoot.querySelector('.list');
            let html='';
            for (let i in items) {
                let item = items[i];
                 html += `<r2-card title="${item.cedula}"  id="c12">
                
                <p>Capital Vencido: ${item.capital_vencido??''}</p>
                <p>Cargos: ${item.cargos??''}</p>
                <p>Cargos Cobrados: ${item.cargos_cobrados??''}</p>
                <p>Dias: ${item.dias ??'' }</p>
                <p>Id Credito: ${item.id_credito?? ''}</p>
                <p>Interes Vencido: ${item.interes_vencido?? ''}</p>
                <p>Mora Desde: ${item.mora_desde ?? ''}</p>
                <p>Tasa: ${item.tasa??''}</p>


                
                <h1 slot="footer">
                Cargos Mora: $ ${item.cargos_mora??0}
                </h1>
                </r2-card>`
            };
            console.log("end search");
            list.innerHTML = html;

        });
    }

    
   
}

customElements.define('r2-search-cards', R2SearchCards);